function main() {
	let isCategoryNavOpen = false;
	const catalogBtn = document.getElementById('header__catalog-btn');
	const categoryNav = document.getElementById('page__category-nav');
	catalogBtn.addEventListener('click', () => {
		isCategoryNavOpen = !isCategoryNavOpen;

		if (isCategoryNavOpen) {
			categoryNav.classList.add('category-nav--open');
			catalogBtn.classList.add('catalog-btn--expanded');
		} else {
			categoryNav.classList.remove('category-nav--open');
			catalogBtn.classList.remove('catalog-btn--expanded');
		}

		categoryNav.setAttribute('aria-expanded', isCategoryNavOpen.toString());
	});
}

document.addEventListener('DOMContentLoaded', () => {
	main();
});
